:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.search-form {
  text-align: center;
  background-color: #3230ae;
  margin-bottom: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
}

body {
  background-color: #f1ebeb;
}

.button-container {
  justify-content: center;
  align-items: center;
  margin-top: 15px;
  display: flex;
}

.load-more {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: red;
  border: none;
  border-radius: 5px;
  padding: 10px;
  font-size: 1.2em;
  transition: background-color .3s ease-in-out, transform .2s ease-in-out;
  display: inline-block;
}

.load-more:hover {
  background-color: #b30068;
  transform: scale(1.05);
}

.hidden {
  display: none;
}

.info {
  justify-content: space-between;
  margin: 0 10px;
  display: flex;
}

.info-item {
  text-align: center;
  color: #333;
  flex-direction: column;
  display: flex;
}

.gallery {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  list-style: none;
  display: grid;
}

.gallery__item {
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery__image:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 180px;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery__link {
  width: 100%;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

